      position: relative;
      .waterBorder {
        width: 401px;
        height: 164px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 18px;
        .waterCost {
          display: flex;
          flex-direction: column;
          align-items: center;
          box-sizing: border-box;
          padding: 20px 10px;
          .button {
            width: 320px;
            height: 52px;
            text-align: center;
            line-height: 52px;
            background: #3ec1ef;
            color: #fff;
            font-size: 30px;
            border-radius: 31px;
          }
        }
      }
    }

    .power {
      width: 484px;
      height: 484px;
    }
  }
  .rightWrap {
    display: flex;
    flex-direction: column;
    .topWrap {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      .gas {
        width: 474px;
