        position: relative;
        .gasContent {
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          margin-top: 18px;
          padding: 1px;
        }
        .gasTitle {
          color: #0cebff;
          font-size: 24px;
          margin-top: 220px;
          text-align: left;
          padding-bottom: 15px;
          border-bottom: 1px solid #0cebff;
          display: flex;
          align-items: center;
          margin-bottom: 24px;
          img {
            width: 72px;
            height: 13px;
            margin-left: 20px;
          }
        }
        .gasData {
          display: flex;
          div {
            width: 50%;
            flex-shrink: 0;
            display: flex;
            .icon {
              width: 65px;
              height: 65px;
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 10px;
              background: #3ec1ef;
              margin-right: 10px;
              img {
                width: 35px;
                height: 35px;
              }
            }
            .text {
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              p {
                color: #0cebff;
                font-size: 24px;
              }
              & p:last-child {
                color: #fff;
                font-size: 18px;
              }
            }
          }
        }
      }
      .supervise {
        width: 590px;
        height: 465px;
        .projectPic {
          width: 100%;
          height: 254px;
          margin-top: 27px;
        }
      }
    }
    .bottomWrap {
      display: flex;
      align-items: center;
      .rent {
        width: 589px;
        height: 311px;
        margin-right: 20px;
      }
      .position {
        width: 477px;
        height: 311px;
        .positionPic {
          margin-top: 25px;
        }
      }
    }
  }
}
</style>